import {createSlice, createAsyncThunk} from '@reduxjs/toolkit';
import axios from 'axios';

const API_URL = 'http://www.kobis.or.kr/kobisopenapi/webservice/rest/boxoffice/searchDailyBoxOfficeList.json';

const API_KEY = 'd984a3b39ba04f6d4989a43be21ef880';

export const getMovieRank = createAsyncThunk("MovieRankSlice/getMovieRank", async (payload, {rejectWithValue}) => {
    let result = null;

    try{
        result = await axios.get(API_URL, {
            params: {
                key: API_KEY,
                //    payload .
                // -->    payload   .
                // --> JSON  payload JSON .
                targetDt: payload.targetDt
            }
        });

        //   HTTP  200    catch  .
        //     .
        if(result.data.faultInfo !== undefined){
            const err = new Error();
            err.response = {status: 500, statusText: result.data.faultInfo.message};
            throw err;
        }
    } catch (err){
        result = rejectWithValue(err.response);
    }
    return result;
});

const MovieRansSlice = createSlice({
    name: 'movieRank',
    initialState: {
        data: null,
        loading: false,
        error: null
    },
    reducers: {},
    extraReducers: {
        [getMovieRank.pending] : (state, {payload}) => {
            return {...state, loading: true}
        },
        [getMovieRank.fulfilled] : (state, {payload}) => {
            return {
                data: payload?.data,
                loading:false,
                error:null
            }
        },
        [getMovieRank.rejected] : (state, {payload}) => {
            return {
                data: payload?.data,
                loading: false,
                error: {
                    code: payload?.status ? payload.status : 500,
                    message: payload?.statusText ? payload.statusText : 'Server Error'
                }
            }
        }
    }
});

export default MovieRansSlice.reducer;