import React, { memo } from 'react';

import {
    Chart as ChartJS,
    CategoryScale,
    LinearScale,
    BarElement,
    Title,
    Tooltip,
    Legend,
} from 'chart.js';

import {Bar} from 'react-chartjs-2';

ChartJS.register(
    CategoryScale,
    LinearScale,
    BarElement,
    Title,
    Tooltip,
    Legend
);

const BarChartView = memo(({chartData}) => {
    /**   */
    const options = {
        indexAxis : 'x',
        response : true,
        plugins : {
            legent: {
                position: 'bottom',
            },
            title: {
                display: true,
                text: '  ',
            }
        },
    };

    /** chart   () */
    const data = {
        // x  
        labels: chartData.movieNm,
        // y    
        datasets: [{
            //  
            label: ' ',
            backgroundColor: '#0066ff44',
            borderColor: '#0066ff',
            borderWidth: 1,
            //    y 
            data: chartData.audiCnt,
        }]
    };

    return (
        <Bar
            data={data}
            options={options}
        />
    );
});

BarChartView.defaultProps = {
    chartData: {
        movieNm: [], audiCnt: []
    }
}

export default BarChartView;